@import '../setup/mixins';
@import '../setup/vars';

.advantages-section {
  text-align: center;
  padding: 80px 0;
  box-shadow: 0 -20px 80px 0 rgba(0, 0, 0, 0.30);
  color: $color__text--primary;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  @media screen and (max-width: 425px) {
    padding: 50px 0; }
  @include element('header') {
    margin-top: 0; }
  @include element('advantage-wrapper') {
    display: flex;
    flex-flow: row nowrap;
    margin: 40px 100px;
    @include modifier('1') {
      justify-content: flex-start; }
    @include modifier('2') {
      justify-content: center; }
    @include modifier('3') {
      justify-content: flex-end; }
    @media screen and (max-width: 425px) {
      margin: 40px 20px; } }
  @include element('advantage') {
    flex: 0 1 600px;
    display: flex;
    flex-flow: row nowrap;
    @media screen and (max-width: 425px) {
      flex-flow: column wrap; } }
  @include element('icon-wrapper') {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    @media screen and (max-width: 425px) {
      flex-flow: row;
      margin-bottom: 15px; } }
  @include element('icon') {
    display: block;
    font-size: 60px;
    background-color: $color__primary;
    color: white;
    padding: 20px 20px 20px 24px;
    width: 80px;
    height: 80px;
    line-height: 80px;
    border-radius: 80px; }
  @include element('text-wrapper') {
    text-align: left;
    margin-left: 20px;
    @media screen and (max-width: 425px) {
      margin-left: 0; } }
  @include element('paragraph') {
    margin: 0;
    @media screen and (max-width: 425px) {
      text-align: center; } }
  @include element('sub-header') {
    margin: 0 0 10px 0;
    @media screen and (max-width: 425px) {
      text-align: center; } } }
